function noop() {}
export default class Timer {
  constructor() {
    this.timerId = null;
  }

  startTimer({
    count = 60,
    stepCallback = noop,
    timeOverCallback = noop,
    interval = 1000,
  } = {}) {
    let _count = count;
    let intervalCallback = () => {
      _count -= 1;
      stepCallback(_count);
      if (_count === 0) {
        timeOverCallback();
        intervalCallback = null;
        this.stopTimer();
      }
    };
    if (this.timerId === null) {
      stepCallback(_count);
      this.timerId = setInterval(intervalCallback, interval);
    }
  }

  stopTimer() {
    clearInterval(this.timerId);
    this.timerId = null;
  }
}
