import * as actionTypes from './actionTypes';

const setBindStatus = (info) => {
  return {
    type: actionTypes.SET_BIND_STATUS,
    payload: info,
  };
};

const setDialogStatus = (info) => {
  return {
    type: actionTypes.SET_DIALOG_STATUS,
    payload: info,
  };
};

const setTipStatus = (info) => {
  return {
    type: actionTypes.SET_TIP_STATUS,
    payload: info,
  };
};

export {
  setBindStatus,
  setDialogStatus,
  setTipStatus,
}