import { isArray } from 'lodash-es';

const createFormAndSubmit = ({ action, method = 'post', inputs = [] }) => {
  if (!document) {
    throw new Error(
      'the envirement you are using is not has a document variable',
    );
  }
  if (!isArray(inputs)) {
    throw new Error('inputs must be a instance of Array');
  }
  const form = document.createElement('form');
  const fragment = document.createDocumentFragment();
  form.action = action;
  form.method = method;
  form.style.display = 'none';
  inputs.forEach(item => {
    const input = document.createElement('input');
    input.type = 'text';
    input.name = item.name;
    input.defaultValue = item.defaultValue;
    fragment.appendChild(input);
  });
  form.appendChild(fragment);
  document.body.appendChild(form);
  form.submit();
  document.body.removeChild(form);
  return form;
};

const loginAfterRegistration = ({ action, account, password, ts, tsh }) => {
  createFormAndSubmit({
    action,
    inputs: [
      {
        name: 'rememberMe',
        defaultValue: 'true',
      },
      {
        name: 'login',
        defaultValue: '登录',
      },
      {
        name: 'analyticsLoginOrigin',
        defaultValue: 'login_action',
      },
      {
        name: 'clipperFlow',
        defaultValue: 'false',
      },
      {
        name: 'showSwitchService',
        defaultValue: 'true',
      },
      {
        name: 'usernameImmutable',
        defaultValue: 'false',
      },
      {
        name: 'username',
        defaultValue: account,
      },
      {
        name: 'password',
        defaultValue: password,
      },
      {
        name: 'hpts',
        defaultValue: ts,
      },
      {
        name: 'hptsh',
        defaultValue: tsh,
      },
    ],
  });
};

const loginAfterBind = ({ action, authToken, hpts, hsHash }) => {
  createFormAndSubmit({
    action,
    inputs: [
      {
        name: 'loginToken',
        defaultValue: authToken,
      },
      {
        name: 'tokenLogin',
        defaultValue: '',
      },
      {
        name: 'hpts',
        defaultValue: hpts,
      },
      {
        name: 'hptsh',
        defaultValue: hsHash,
      },
      {
        name: 'wechatLogin',
        defaultValue: 'true',
      },
    ],
  });
};

const transformObjectToArray = obj => {
  return Object.keys(obj).map(key => obj[key]);
};

export { loginAfterRegistration, loginAfterBind, transformObjectToArray };
