export default copywriting => {
  if (typeof copywriting !== 'string')
    throw new Error('copywriting must be a string!');
  const strLength = copywriting.length;
  const array = [];
  let chunk = '';
  for (let i = 0; i < strLength; i += 1) {
    const char = copywriting.charAt(i);
    if (char === '{' || char === '}') {
      if (chunk) {
        array.push(chunk);
        chunk = '';
      }
    } else {
      chunk += char;
    }
  }
  if (chunk) {
    array.push(chunk);
  }
  return array;
};
