import React from 'react';
import Dialog from 'rc-dialog';

import {
    ACKNOWLEDGE,
} from '../../config/copywriting';

import'./Dialog.module.scss';
import styles from './index.module.scss';

const noop = () => {};

const getDefaultOperate = (cb) => [{
    text: ACKNOWLEDGE,
    main: true,
    onClick: cb || noop,
}];
//

const doBeforeClose = (onClick, onClose) => {
    onClick();
    onClose();
}

const renderOperates = (operates, onClose) => {
    const _operates = Array.isArray(operates) ? operates.slice(0, 2) : getDefaultOperate(operates);
    const doms = _operates.map(({text, main, onClick = noop}, index) => {
        return <div key={index} onClick={() => doBeforeClose(onClick, onClose)} className={`${styles.button} ${main ? styles.main : ''}`}>{text}</div>
    })
    doms.length > 1 && doms.splice(1, 0, <div key="verti" className={styles.vertical}></div>);
    return doms;
};

const CustomerDialog = ({visible = true, onClose, title, content, operates}) => {
    return (
        <Dialog visible={visible}
                closable={false} 
                onClose={onClose}
                title={title}
                animation="fade"
                maskAnimation="fade"
        >
           <div className={styles.content}>
             <span>{content}</span>
           </div>
           <div className={styles.separator}></div>
           <div className={styles.operate}>
            {
                renderOperates(operates, onClose)
            }
           </div>
        </Dialog>
    )
}

CustomerDialog.defaultProps = {
    operates: getDefaultOperate(),
    onClose: noop,

}

export default CustomerDialog;
