import md5 from 'js-md5';
import textUtilZh from './textUtil.js';
import textUtilEn from './textUtilEn.js';
import config from './config.js';

const LETTERS = ['a','b','c','d','e','f','g','h','i','j',
                 'k','l','m','n','o','p','q','r','s','t',
                 'u','v','w','x','y','z'];

export default {
    /**
     * 设置title
     */
    setWindowTitle: function(title){
        document.title = title;
    },

    /**
     * 获取URL参数
     */
    getQueryParams(name) {
        var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
        var r = window.location.search.substr(1).match(reg);
        if (r != null) return unescape(r[2]); return null;
    },

    getLangEnv() {
        if (config.getLangEnv() === 'zh') {
            return textUtilZh;
        } else {
            return textUtilEn;
        }
    },

    getSignByParam(paramObject) {
        let string = '';
        const sortedParam = this.sortByFirstLetter(paramObject);
        for(let i in sortedParam){
            if(sortedParam[i] !== undefined){
                string+=`${i.toLowerCase()}=${sortedParam[i]}`;
            }
        }
        return md5(string);
    },

    sortByFirstLetter(needSortObject){
        let sortedObject = {};
        let i = 0;
        while(i<LETTERS.length){
            const letter = LETTERS[i];
            for(let j in needSortObject){
                const firstLetter = j.slice(0,1);
                if(letter === firstLetter.toLowerCase()){
                    sortedObject[j] = needSortObject[j];
                }
            }
            i+=1;
        }
        return sortedObject;
    },
    hidePartOfPhoneNumber(phoneNumber){
        const phoneNumberString = `${phoneNumber}`;
        const starCount = 5;
        try{
            let leftNumberCount;
            let rightNumberCount;
            leftNumberCount = rightNumberCount = (11 - starCount)/2;
            const reg = new RegExp(`^(\\d{${leftNumberCount}})\\d{${starCount}}(\\d{${rightNumberCount}})$`);
            const starString = new Array(5).fill('*').join('');
            return phoneNumberString.replace(reg,`$1${starString}$2`);
        }catch(e){
            return phoneNumberString;
        }
    },
    detectOffLine(self) {
        let textUtil = this.getLangEnv();
        if ("onLine" in navigator) {
            if (navigator.onLine === false) {
                self.setState({
                    verificationWarningTxt: textUtil.offLineTip
                });
                return;
            }
        }
    }
}