import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { TIP_LEVEL_MAP } from '../../config/propsPermittedValueMap';
import { transformObjectToArray } from '../../utils/commonUtils';

import styles from './index.module.scss';

const LEVEL_LIST = transformObjectToArray(TIP_LEVEL_MAP);

class BasicInput extends Component {
  static propTypes = {
    inputType: PropTypes.string,
    inputName: PropTypes.string,
    tipLevel: PropTypes.oneOf(LEVEL_LIST),
    showTipIcon: PropTypes.bool,
    placeHolder: PropTypes.string.isRequired,
    children: PropTypes.oneOfType([PropTypes.element, PropTypes.string]),
    onFocus: PropTypes.func,
    onChange: PropTypes.func.isRequired,
    onBlur: PropTypes.func,
    value: PropTypes.string,
    useDefaultTip: PropTypes.bool, // 是否使用该组件提供的默认输入提示,默认为 true
  };

  static defaultProps = {
    inputType: 'text',
    inputName: '',
    tipLevel: TIP_LEVEL_MAP.DEFAULT,
    showTipIcon: true,
    value: '',
    onFocus: () => {},
    onBlur: () => {},
    useDefaultTip: true,
  };

  constructor(props) {
    super(props);
    this.state = {
      value: '',
      clearLevel: TIP_LEVEL_MAP.DEFAULT,
    };
  }

  componentWillReceiveProps(nextProps) {
    const { value } = this.props;
    const { value: nextValue, tipLevel } = nextProps;
    if (nextValue !== value) {
      this.setState({
        value: nextValue,
        clearLevel: nextValue.length ? 'clear' : tipLevel, 
      });
    }
  }

  onChange(ev) {
    const { onChange } = this.props;
    const {
      target: { value },
    } = ev;
    this.setState({
      value,
    });
    onChange(ev);
  }

  clearInput = () => {
    const { onChange } = this.props;
    this.setState({
      value: '',
    })
    onChange({ target: { value: '' }});
  }

  render() {
    const { value, clearLevel } = this.state;
    const {
      children,
      inputType,
      inputName,
      placeHolder,
      showTipIcon,
      onFocus,
      tipLevel,
      useDefaultTip,
      onBlur,
    } = this.props;
    return (
      <div className={styles.wrapper}>
        <div className={styles.inputWrapper}>
          <input
            className={styles.input}
            type={inputType}
            name={inputName}
            value={value}
            placeholder={placeHolder}
            onFocus={onFocus}
            onChange={this.onChange.bind(this)}
            onBlur={onBlur}
          />
          {useDefaultTip && showTipIcon ? (
            <span className={`${styles.tipIcon} ${styles[clearLevel]}`}
                  onClick={this.clearInput}
            />
          ) : null}
        </div>
        {useDefaultTip ? (
          <p className={`${styles.tipText} ${styles[tipLevel]}`}>{children}</p>
        ) : null}
      </div>
    );
  }
}

export default BasicInput;
