import React, { Component } from 'react';
import {connect} from 'react-redux';
import PropTypes from 'prop-types';

import {CONFIRM, CANCEL, DEFAULT_MODAL_TITLE} from '../../config/copyWriting';
import {DIALOG_BUTTON_TYPE} from '../../config/propsPermittedValueMap';
import * as actions from '../../store/actions';

import styles from './index.module.scss';

class BasicModal extends Component {
  static propTypes = {
    title: PropTypes.string, // dialog - 标题
    content: PropTypes.string, // dialog - 内容
    needTitle: PropTypes.bool,  // dialog - 是否需要标题区域
    okButtonText: PropTypes.string, // dialog - 确认按钮文本
    cancelText: PropTypes.string, // dialog - 取消按钮文本
    onOk: PropTypes.func, // dialog - 确认按钮回调方法
    okButtonType: PropTypes.string, // dialog - 确认按钮类型
    tip: PropTypes.string, // tip - 内容
  }

  static defaultProps = {
    okButtonText: CONFIRM,
    cancelText: CANCEL,
    title: DEFAULT_MODAL_TITLE,
    needTitle: true,
    okButtonType: DIALOG_BUTTON_TYPE.NORMAL,
  }

  maskClick = () => {
    this.handleCancel();
  }

  handleCancel = () => {
    const {setDialogStatus} = this.props;
    setDialogStatus(false);
  }

  handleConfirm = () => {
    const {setDialogStatus, onOk} = this.props;
    setDialogStatus(false);
    onOk && onOk();
  }

  render() {
    const {
      setTipStatus,
      okButtonText,
      cancelText,
      title,
      content,
      isDialogShow,
      okButtonType,
      needTitle,
      tip,
      isTipShow,
    } = this.props;
    if (isTipShow) {
      setTimeout(() => {
        setTipStatus({tip, isTipShow: false});
      }, 1500);
    }

    return (
      <React.Fragment>
        <div className={`${styles.dialogWrapper} ${isDialogShow ? styles.dialogOn : ''}`}>
          <div className={styles.dialog}>
            {needTitle && <div className={styles.dialogTitle}>
              {title}
              <span className={styles.closeIcon} onClick={this.handleCancel}></span>
            </div>}
            <div className={styles.dialogContent}>{content}</div>
            <div className={styles.dialogOperator}>
              <button className={`${styles.dialogConfirm} ${styles[okButtonType]}`} onClick={this.handleConfirm}>{okButtonText}</button>
              <button className={styles.dialogClose} onClick={this.handleCancel}>{cancelText}</button>
            </div>
          </div>
          <div className={styles.mask} onClick={this.maskClick}></div>
        </div>
        <div className={`${styles.tipWrapper} ${isTipShow ? styles.tipOn : ''}`}>
          <label>{tip}</label>
        </div>
      </React.Fragment>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    isDialogShow: state.isDialogShow,
    tip: state.tipInfo.tip,
    isTipShow: state.tipInfo.isTipShow,
  };
};

export default connect(mapStateToProps, actions)(BasicModal);
